            <!-- <a href="#" style="font-size: 12px;">Online First, doi:10.32604/cmes.2020.011067 </a> -->
            <h3>
               Exponential anti-synchronization of reaction-diffusion
memristive neural networks with leakage term and
time-varying delays
            </h3>
            <span style="display: block;color: #4f5671;line-height: 25px;margin-bottom: 10px;font-size: 12px;">
                Yanyi Cao
            </span>
            <!-- <ul style="padding: 0;font-size: 12px;color: #4f5671;">
