            <span><a href="https://stduestceducn-my.sharepoint.com/:b:/g/personal/201921080433_std_uestc_edu_cn/Eb8AnH09P3tDhLTPtOG-YKsBj59q-KFNW1dRty-g8dvY3w?e=snaxRC" rel="nofollow" class="pdf">Download PDF</a>  </span>
        </div>

        <div class="abstract">
            <h3>Abstract</h3>
            <p>
               In this paper, the exponential anti-synchronization problem is studied for a
class of memristive neural networks (MNNs) with leakage term, reaction-diffusion term
and time-varying delays. First, by designing two different types of controllers, we can
use them to investigate the exponential anti-synchronization problem for the proposed
systems. Secondly, via adopting Lyapunov functional method, the drive-response theory
and some inequality techniques, several sufficient conditions are derived to ensure the
global exponential anti-synchronization problems of the proposed reaction-diffusion
MNNs with leakage term and time-varying delays. Specifically, the delays could be
time-varying delays or constants in the leakage term. Compared with the previously
research results, the proposed neural network models herein are more general, and
the obtained results consider the diffusion effects, leakage delays and time-varying
delays, and those results can improve and the previously obtained results. At last, two
numerical examples are provided to demonstrate the validity and effectiveness of the
proposed theoretical results.
            </p>
